.post_item {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 330px;
    margin: 20px;
    padding: 8px;
    border: 2px solid teal;
}

.post_item img {
    width: 200px;
    box-shadow: 8px 12px 14px 4px #303030;
}

.post_title {
    margin: 20px 5px;
}