.post_with_comment {
    font-size: 25px;
    max-width: 1200px;
    margin: auto;
    margin-top: 80px;
}

.post_item_title {
    margin: 20px 0;
    text-align: center;
}

.post_item_body {
    margin: auto;
    padding-bottom: 50px;
    width: 1000px;
    border-bottom: 1px solid black;
}

.post_comments {
    font-size: 20px;
}

.comment {
    margin: 10px 0;
}

.comment_wrapper {
    display: flex;
    align-items: center;
}

.time {
    margin-left: 30px;
    color: rgb(161, 152, 152);
}

textarea {
    outline: none;
    margin-top: 10px;
    resize: none;
    width: 70%;
    padding: 8px;
    font-size: 20px;
    border: none;
    border-bottom: 1px solid rgb(161, 152, 152);
    border-radius: 10px;
}

.comment_area {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.comment_area button {
    cursor: pointer;
    outline: none;
    border: 1px solid rgb(161, 152, 152);
    background-color: white;
    margin: 20px;
    padding: 8px;
}